body{
  padding:0;
  margin: 0;
  text-align: center;
  font-family: 'Fira Code', monospace;
  width:100%;
  height:100%;
}

img{
  display:inline-block;
  padding-left:1%;
}
.footer{
  text-align: center;
}
.firstimg{
  display:inline-block;
  padding-left:0%;
}

.space{
  margin-top:3%;
}

.break{
  margin-top:6%;
}
.spaceskill{
  margin-top:2%;
}

.technical{
  width: 70%;
  margin-left: 30%;
  margin-right: auto;
  text-align: left;
  margin-top: 14%;
  position:absolute;
  color:#FFFFFF;
  font-size:70%;
  font-weight:lighter;
  display:block;
}

.text{
  text-align: left;
  width:65%;
  font-size:150%;
  font-family: ui-sans-serif,system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
  font-weight:bolder
} 
.container{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  position:absolute;
  
}


.bottom{
  width: 100%;
  text-align: left;
  margin-top: 53%;
  position:absolute;
  color:#FFFFFF;
  font-size:70%;
  font-weight:lighter;
  display:block;
  background-image:linear-gradient(#162747, #444242);
 
}
.contact{

  width:44%;
  margin:auto;
  margin-top:-2%;
}
.contactheader{
  text-align: center;
}

.container h1:nth-child(1) {
  color: #FFFFFF;
  font-weight: 800;
  font-size: 130%;
  margin: 5% 0 0 35%;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  animation: type 2s steps(40,end) forwards;
  width:17%;
}
.container h1:nth-child(2) {
  color: #ff1d1d;
  opacity:0;
  font-weight: 800;
  font-size: 130%;
  margin: 1% 0 0 35%;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  width:39%;
  animation: shring-animation 2.5s steps(65,end) 2s forwards, blink .5s step-end infinite alternate;
}
.container h1:nth-child(3) {
  color: #1fdf59;
  opacity:0;
  font-weight: 800;
  font-size: 130%;
  margin: 1% 0 0 35%;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  width:39%;
  animation: animation 2.5s steps(65,end) 2s forwards;
}

#mb-3{
  align-items:center;
  background-color:#454547;
  margin-left:10%;
  height:30px;
  width:80%;
  color:white;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-clip: border-box;
  border: 2px solid #FFFFFF;
  border-radius: 0.25rem;
  
}


#mb-5{
  color:white;
  background-color:#454547;
  height:100px;
  width:80%;
  margin-left:10%;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-clip: border-box;
  border: 2px solid #FFFFFF;
  border-radius: 0.25rem;
  font-family:ui-sans-serif,system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;

}
#contactbutton{
  margin-left:10%;
  width:81%;
  padding:1%;
  background-color: #162747;
  color:white;
  font-family: 'Fira Code', monospace;
  font-weight:bold;
  font-size:150%;

}
#contactbutton:hover {background-color: #174292}

#contactbutton:active {
  background-color: #232741;
  box-shadow: 0 2px #111111;
  transform: translateY(0px);
}

@keyframes animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }
  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #1fdf59;
  }
  50% {
    background-position: 150px 0;
    opacity: 1;
    
  }
  100% {
    background-position: 400% 0;
    opacity: 1;
    
  }
}

@keyframes shring-animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }
  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }
  50% {
    background-position: 150px 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }
  100% {
    background-position: 400% 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }
}

@keyframes type {
  0% {
    width: 0;
  }
  1%, 99%{
    border-right: 1px solid #FFFFFF;
  }
  100%{
    border-right: none;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}




/* ---- particles.js container ---- */

#particles-js{
width: 100%;
height: 100%;
background-color: #232741;
background-size: cover;
background-position: 50% 50%;
background-repeat: no-repeat;
}
